body {
  font-family: "Open Sans",
  sans-serif; 
  font-size: 20px;
  text-align: center;
  line-height: 1.5;
  background: url(/MyPortfolio/static/media/background.1b56fcf8.jpg) no-repeat center center fixed;
  background-size: cover;
}


.Home,
.Contact {
  width: 100%;
  display: inline-block;
  margin: 0 30%;
  padding: 30px;
  background: rgba(255, 255, 255, 0.507);
  border: rgb(250, 250, 250) solid 2px;
}

.Projects {
  width: 100%;
  display: inline-block;
  margin: 0 30%;
  padding: 30px;
  
}

.individual {
  border: rgb(250, 250, 250) solid 2px;
  background: rgba(255, 255, 255, 0.507);
  padding: 20px;
  margin-bottom: 20px;

}

div {
  width: 70%;
  padding: 10% 2%;
}





p {
  text-align: left;
  font-size: 14px;
  color: rgb(63, 44, 99);
  font-family: 'Comic Neue', cursive;
}


.individual,
.reachout {
  max-width:100%;

}

h1 {
  text-shadow: 2px 2px rgba(134, 114, 143, 0.521);
  color: rgb(134, 114, 143);
  font-family: 'Poppins', sans-serif;
}

h3 {
  color: rgba(134, 114, 143, 0.658);
}


@media screen and (min-width: 520px) {


  .image {
    float: left;
    margin-right: 10px;
    width: 75%;
  }

  .self {
    float: left;
    margin-right: 10px;
    width: 50%;
  }

  div {
    width: 70%;
    padding: 2.4% 5%;
  }
}
nav {
    opacity: 70%;
    position: fixed;
    padding: 5px;
    top: 0;
    left: 0;
    right: 0;
    font-family: 'Inconsolata', monospace;
    width: 100%;
    background-color: rgba(226, 186, 226, 0.466);
    display: flex;
    justify-content: space-evenly;
  }

  nav a {
    float: left;
    padding: 1px;
    color: rgb(255, 255, 255);
    text-decoration: none;
    font-size: 23px;
    width: 33.33%;
    text-align: center;
  }
  
  nav a:hover {
    text-shadow: 2px 2px rgba(134, 114, 143, 0.521);
  }



